<template>
  <div>
    <canvas id="canvas" type="2d" class="canvas"></canvas>

    <img class="poster" :src="makeUrl" @click="saveImage" />
  </div>
</template>
<script>
import Poster from "./Poster.js";
export default {
  props: {
    w: {
      type: Number,
      default: 375,
    },
    h: {
      type: Number,
      default: 667,
    },
    isWX: Boolean,
    cvData: {
      type: Object,
      required: false,
      default: () => {
        return {
          image: [
            {
              w: 200,
              h: 200,
              img: "https://image.01lb.com.cn/image/xEF74tYb8nmXJw3TrV5b7uMSmbKfDbGadzMWvbND.png",
              // img: require("@/assets/logo.png"),
              x: (375 - 200) / 2,
              y: (667 - 200) / 2,
            },
          ],
          text: [
            {
              size: 60,
              font: "Fancier",
              color: "#c0c0c0",
              x: 375 / 2,
              y: 20,
              align: "center",
            },
          ],
        };
      },
    },
  },
  data() {
    return {
      cv: null,
      ctx: null,

      makeUrl: "",
    };
  },
  mounted() {
    const pt = new Poster({id: "canvas", data: this.cvData});
    pt.init().then(data => {
      console.log("data", data);
      this.makeUrl = data;
    });
    // if (this.isWX) {
    //   wx.createSelectorQuery()
    //     .in(this)
    //     .select("#canvas")
    //     .fields({ node: true, size: true })
    //     .exec((res) => {
    //       this.cv = res[0].node;
    //       this.ctx = this.cv.getContext("2d");
    //       this.init();
    //     });
    // } else {
    //   this.cv = document.getElementById("canvas");
    //   this.ctx = this.cv.getContext("2d");
    //   this.init();
    // }
  },
  methods: {
    async init() {
      let dpr = this.getRatio();
      console.log("dpr", dpr);
      this.cv.width = this.w * dpr;
      this.cv.height = this.h * dpr;
      this.ctx.scale(dpr, dpr);

      await this.drawImage();
      this.drawFont();
      this.$nextTick(() => {
        this.makeImage();
      });
    },

    // 
    drawFont() {
      const ctx = this.ctx;
      let text = this.cvData.text;
      for (let i = 0; i < text.length; i++) {
        let item = text[i];
        ctx.fillStyle = item.color;
        ctx.textAlign = item.align;
        ctx.font = `normal ${item.weight ? "bold" : "normal"} ${
          item.size
        }px FreebooterScript`;
        ctx.fillText(item.font, item.x, item.y + item.size);
      }
    },

    // 
    getImage(src) {
      return new Promise((resolve, reject) => {
        let img;
        if(this.isWX) {
          img = this.cv.createImage()
        } else {
          img = new Image();
        }
        img.src = src;
        img.setAttribute("crossOrigin", "Anonymous");
        img.onload = () => {
          resolve(img);
        };
      });
    },
    async drawImage() {
      const ctx = this.ctx;
      let image = this.cvData.image;
      for (let i = 0; i < image.length; i++) {
        let item = image[i];
        let img = await this.getImage(image[i].img);

        if (item.w) {
          ctx.drawImage(img, item.x, item.y, item.w, item.h);
        } else {
          ctx.save();
          ctx.beginPath();
          ctx.arc(item.x + item.r, item.y + item.r, item.r, 0, 2 * Math.PI);
          ctx.fillStyle = "#fff";
          ctx.fill();
          ctx.clip();
          ctx.drawImage(img, item.x, item.y, item.r * 2, item.r * 2);
          ctx.restore();
        }
      }
    },

    getRatio() {
      return this.isWX ? wx.getSystemInfoSync().pixelRatio : this.calcRatio();
    },
    calcRatio() {
      const ctx = this.ctx;
      // 
      let devicePixelRatio = window.devicePixelRatio || 1;
      // canvas
      let backingStoreRatio =
        ctx.webkitBackingStorePixelRatio ||
        ctx.mozBackingStorePixelRatio ||
        ctx.msBackingStorePixelRatio ||
        ctx.oBackingStorePixelRatio ||
        ctx.backingStorePixelRatio ||
        1;
      // canvas
      let ratio = devicePixelRatio / backingStoreRatio;
      return ratio;
    },

    makeImage() {
      if (this.isWX) {
        wx.canvasToTempFilePath({
          destWidth: 750,
          destHeight: 1334,
          canvas: this.cv,
          success: (res) => {
            this.makeUrl = res.tempFilePath;
            console.log(this.makeUrl);
          },
          fail(err) {},
        });
      } else {
        this.makeUrl = this.cv.toDataURL("image/png");
      }
    },

    base64ToBlob(code) {
      let parts = code.split(";base64,");
      let contentType = parts[0].split(":")[1];
      let raw = window.atob(parts[1]);
      let rawLength = raw.length;
      let uInt8Array = new Uint8Array(rawLength);
      for (let i = 0; i < rawLength; ++i) {
        uInt8Array[i] = raw.charCodeAt(i);
      }
      return new Blob([uInt8Array], {
        type: contentType,
      });
    },
    saveImage() {
      if (this.isWX) {
        wx.saveImageToPhotosAlbum({
          filePath: this.posterImg,
          success: (res) => {
            toast("");
            this.hidePoster();
          },
          fail: (res) => {
            this.authPic = true;
          },
        });
      } else {
        let a = document.createElement("a");
        a.href = URL.createObjectURL(this.base64ToBlob(this.makeUrl));
        a.download = "";
        a.click();
      }
    },
  },
};
</script>
<style lang="less" scoped>
.canvas {
  border: 1px solid #f2f2f2;
  width: 375px;
  height: 667px;
  position: absolute;
  top: -200%;
}
.poster {
  border: 1px solid #f2f2f2;
  width: 375px;
  height: 667px;
}
</style>