<template>
  <div>
    <p class="font-preview">{{ msg }}</p>
    <input class="all-center input" v-model="msg" />
    <!-- <vueSeamlessScroll :data="scrolls" :class-option="option" class="vue-seamless-scroll">
      <div class="item" v-for="(v, i) in scrolls" :key="i">{{ v }}</div>
    </vueSeamlessScroll> -->
  </div>
</template>
<script>
// import vueSeamlessScroll from 'vue-seamless-scroll'
export default {
  // components: {
  //   vueSeamlessScroll
  // },
  data() {
    return {
      msg: "",
      scrolls: [],
      option: {
        direction: 0,
        hoverStop: false,
        singleHeight: 50
      }
    }
  },
  watch: {
    msg(val) {
      !val && (this.scrolls = []);
      this.scrolls = val.split(" ");
    }
  },
  created() {
    // this.scrolls = "!".split("");
    // console.log(this.scrolls);
  }
}
</script>
<style lang="less" scoped>
.font-preview {
  padding: 100px;
  font-family: FreebooterScript, shaonv;
  font-size: 3em;
  font-weight: 500;
}
.all-center {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.input {
  z-index: 9999;
}
.vue-seamless-scroll {
  height: 300px;
  overflow: hidden;
  .item {
    height: 50px;
  }
}
