export default function Poster(params) {
	this.w = params.width || 375;
	this.h = params.height || 667;
	this.isWx = params.isWx || false;
	this.cvData = params.data;
	this.cvId = params.id;
	this.cv = null;
	this.ctx = null;
	this.imageData = "";
	this.contextEl = this.isWx ? params.that : "";

	// test
	// this.cvData = {
	// 	image: [
	// 		{
	// 			w: 200,
	// 			h: 200,
	// 			img: "https://image.01lb.com.cn/image/xEF74tYb8nmXJw3TrV5b7uMSmbKfDbGadzMWvbND.png",
	// 			// img: require("@/assets/logo.png"),
	// 			x: (375 - 200) / 2,
	// 			y: (667 - 200) / 2,
	// 		},
	// 	],
	// 	text: [
	// 		{
	// 			size: 60,
	// 			font: "Fancier",
	// 			color: "#c0c0c0",
	// 			x: 375 / 2,
	// 			y: 20,
	// 			align: "center",
	// 		},
	// 	],
	// };

	if (!this.cvId || !this.cvData) {
		throw new Error("!");
	}
	if (this.isWx && !this.contextEl) {
		throw new Error("this!");
	}

	this.init = async function () {
		console.log("");
		await this.initCv();
		await this.startDraw();
		await this.createImage();
		return Promise.resolve(this.imageData);
	};

	this.initCv = function () {
		return new Promise((resolve, rejeect) => {
			if (this.isWx) {
				console.log("", this.cvId);
				wx.createSelectorQuery()
					.in(this.contextEl)
					.select("#" + this.cvId)
					.fields({ node: true, size: true })
					.exec((res) => {
						this.cv = res[0].node;
						this.ctx = this.cv.getContext("2d");
						console.log(this.cv, "cccccccv");
						resolve();
					});
			} else {
				this.cv = document.getElementById(this.cvId);
				this.ctx = this.cv.getContext("2d");
				resolve();
			}
		});
	};

	this.startDraw = async function () {
		let dpr = this.getRatio();
		console.log("dpr", dpr);
		this.cv.width = this.w * dpr;
		this.cv.height = this.h * dpr;
		this.ctx.scale(dpr, dpr);

		await this.drawImage();
		this.drawFont();
		// setTimeout(() => {
		//     this.createImage();
		// }, 0);
	};

	// 
	this.drawFont = function () {
		const ctx = this.ctx;
		let text = this.cvData.text;
		for (let i = 0; i < text.length; i++) {
			let item = text[i];
			ctx.fillStyle = item.color;
			ctx.textAlign = item.align;
			ctx.font = `normal ${item.weight ? "bold" : "normal"} ${item.size}px normal`;
			ctx.fillText(item.font, item.x, item.y + item.size);
		}
	};

	// 
	this.getImage = function (src) {
		return new Promise((resolve, reject) => {
			let img;
			if (this.isWx) {
				img = this.cv.createImage();
			} else {
				img = new Image();
			}
			img.src = src;
			img.setAttribute("crossOrigin", "Anonymous");
			img.onload = () => {
				resolve(img);
			};
		});
	};
	this.drawImage = async function () {
		const ctx = this.ctx;
		let image = this.cvData.image;
		for (let i = 0; i < image.length; i++) {
			let item = image[i];
			let img = await this.getImage(image[i].img);

			if (item.w) {
				ctx.drawImage(img, item.x, item.y, item.w, item.h);
			} else {
				ctx.save();
				ctx.beginPath();
				ctx.arc(item.x + item.r, item.y + item.r, item.r, 0, 2 * Math.PI);
				ctx.fillStyle = "#fff";
				ctx.fill();
				ctx.clip();
				ctx.drawImage(img, item.x, item.y, item.r * 2, item.r * 2);
				ctx.restore();
			}
		}
	};

	this.getRatio = function () {
		return this.isWx ? wx.getSystemInfoSync().pixelRatio : this.calcRatio();
	};
	this.calcRatio = function () {
		const ctx = this.ctx;
		// 
		let devicePixelRatio = window.devicePixelRatio || 1;
		// canvas
		let backingStoreRatio =
			ctx.webkitBackingStorePixelRatio ||
			ctx.mozBackingStorePixelRatio ||
			ctx.msBackingStorePixelRatio ||
			ctx.oBackingStorePixelRatio ||
			ctx.backingStorePixelRatio ||
			1;
		// canvas
		let ratio = devicePixelRatio / backingStoreRatio;
		return ratio;
	};

	this.createImage = function () {
		return new Promise((resolve, reject) => {
			if (this.isWx) {
				wx.canvasToTempFilePath({
					destWidth: 750,
					destHeight: 1334,
					canvas: this.cv,
					success: (res) => {
						this.imageData = res.tempFilePath;
						resolve(this.imageData);
					},
					fail(err) {},
				});
			} else {
				this.imageData = this.cv.toDataURL("image/png");
				resolve(this.imageData);
			}
		});
	};

	// this.base64ToBlob = function (code) {
	//     let parts = code.split(";base64,");
	//     let contentType = parts[0].split(":")[1];
	//     let raw = window.atob(parts[1]);
	//     let rawLength = raw.length;
	//     let uInt8Array = new Uint8Array(rawLength);
	//     for (let i = 0; i < rawLength; ++i) {
	//         uInt8Array[i] = raw.charCodeAt(i);
	//     }
	//     return new Blob([uInt8Array], {
	//         type: contentType,
	//     });
	// };
	// this.saveImage = function () {
	//     if (this.isWx) {
	//         wx.saveImageToPhotosAlbum({
	//             filePath: this.posterImg,
	//             success: (res) => {
	//                 toast("");
	//                 this.hidePoster();
	//             },
	//             fail: (res) => {
	//                 this.authPic = true;
	//             },
	//         });
	//     } else {
	//         let a = document.createElement("a");
	//         a.href = URL.createObjectURL(this.base64ToBlob(this.imageData));
	//         a.download = "";
	//         a.click();
	//     }
	// };
}
